<template>
<div class="o2-switch" :class="['is-' + switchState]" @click="toggleState">
  <div class="o2-switch-node"></div>
</div>
</template>

<script>
/**
 * o2-switch
 * @module components/switch
 * @desc 
 * @param {boolean} [checked=false] - 
 * @param {boolean} [disabled=false] - 
 * @param {boolean} [loading=false] - loading
 * @param {callback} [onChange] - 
 *
 * @example
 * <o2-switch checked="true" disabled="false"></o2-switch>
 */
export default 
{
  name: 'o2-switch',
  props: {
    checked: {
      type: Boolean,
      default: false
    },
    disabled: {
      type: Boolean,
      default: false
    },
    loading: {
      type: Boolean,
      default: false
    },
    onChange: {
      type: Function,
      default: function() {}
    }
  },
  computed: {
    switchState: function() {
      if (this.disabled) {
          return 'disabled';
      } else if (this.loading) {
          return 'loading';
      } else if (this.checked) {
          return 'on';
      } else {
          return 'off';
      }
    }
  },
  methods: {
    /*
     * 
     */
    toggleState: function() {
      if(this.disabled || this.loading) return;
      this.onChange(!this.checked);
    },

    /*
     * 
     */
    getState: function() {
      return this.checked;
    }
  }
};
</script>



