<template>
  <button
    :type="nativeType"
    :class="[
      'o2-button',
      'o2-button--' + type,
      'o2-button--' + size,
      {
        'is-disabled': disabled,
        'is-loading': loading
      }
    ]"
    :disabled="disabled"
    @click="handleClick"
  >
    <i v-if="loading" class="o2-icon-loading"></i>
    <span class="o2-button-text"><slot></slot></span>
  </button>
</template>

<script>
/**
 * o2-header
 * @module components/button
 * @desc 
 * @param {string} [type=default] -  default, primary, danger
 * @param {boolean} [disabled=false] - 
 * @param {string} [size=normal] -  normal, mini, small, large
 * @param {string} [native-type] -  type 
 * @param {slot} - 
 *
 * @example
 * <o2-button size="large" type="primary"></o2-button>
 */
export default {
  name: 'o2-button',

  methods: {
    handleClick(e) {
      this.$emit('click', e);
    }
  },

  props: {
    disabled: Boolean,
    loading: Boolean,
    nativeType: String,
    type: {
      type: String,
      default: 'default',
      validator(value) {
        return [
          'default',
          'danger',
          'primary'
        ].indexOf(value) > -1;
      }
    },
    size: {
      type: String,
      default: 'normal',
      validator(value) {
        return [
          'mini',
          'small',
          'normal',
          'large'
        ].indexOf(value) > -1;
      }
    }
  }
};
</script>



